        this.houseList.forEach((obj) => {
            obj.value = obj.id;
            let unitNum = '';
            if (obj.unitNum !== '0' && obj.unitNum !== 0) {
                unitNum = obj.unitNum + '单元';
            }
            obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' ' + unitNum + obj.floorNum + '层' + obj.roomNum + '室';
        });
        return this.houseList;
    }
