import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import houseSelect from '@/components/common-components/houseSelect';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 声明周期钩子
        this.model = { details: '', type: 'type1' };
        this.fields = [
            {
                type: 'textarea',
                modelKey: 'details',
                props: { placeholder: '请描述您要报修的详情...', maxlength: 100, autoExpand: true, autofocus: true },
                rules: { required: true },
                messages: {
                    required: '请输入报修内容'
                }
            },
            {
                type: 'select',
                modelKey: 'type',
                label: '报修类型',
                props: {
                    placeholder: '请输入',
                    type: 'text',
                    options: [
                        {
                            text: '开锁服务',
                            value: 'type1'
                        },
                        {
                            text: '公共报修',
                            value: 'type2'
                        },
                        {
                            text: '个人报修',
                            value: 'type3'
                        }
                    ]
                }
            }
        ];
        this.houseId = '';
        // tab切换配置
        this.selectedLabelDefault = 'repair';
        this.tabBarList = [{
                label: '快捷报修',
                value: 'repair'
            }, {
                label: '报修记录',
                value: 'repairList'
            }];
        // 
        this.page = 1;
        this.isShowMore = true;
        this.repairList = [];
    }
    activated() {
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getBindingInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } });
                    }
                    else if (response.data.sessionId) {
                        this.$store.commit('SET_SESSION_ID', response.data.sessionId);
                        this.$store.commit('SET_USER_INFO', response.data.userInfo);
                        this.getHouseList();
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    tabBarChange(value) {
        if (value === 'repairList') {
            this.page = 1;
            this.repairList = [];
            this.getRepairList();
        }
    }
    showMoreClick() {
        this.page++;
        this.getBillListByHouseId();
    }
    submitHandler(e) {
        e.preventDefault();
        let houseInfo = this.getHouseInfo();
        let query = {};
        query.accountId = houseInfo.accountId;
        query.address = houseInfo.text;
        query.houseId = houseInfo.id;
        query.floorId = houseInfo.floorId;
        query.type = this.model.type;
        query.details = this.model.details;
        this.$store.dispatch('addRepair', query).then((response) => {
            if (response.success) {
                this.model = { details: '', type: 'type1' };
                this.selectedLabelDefault = 'repairList';
                this.tabBarChange('repairList');
                this['$createDialog']({
                    type: 'alert',
                    content: '报修成功, 随后会有工作人员联系您！',
                    icon: 'cubeic-ok'
                }).show();
            }
        });
    }
    get houseOptions() {
        this.$store.state.houseList.forEach((obj) => {
            obj.value = obj.id;
            obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' ' + obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室';
        });
        return this.$store.state.houseList;
    }
    get userInfo() {
        return this.$store.state.userInfo;
    }
    getHouseInfo() {
        let info = {};
        this.$store.state.houseList.forEach((obj) => {
            if (obj.id === this.houseId) {
                info = obj;
            }
        });
        return info;
    }
    getRepairList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getRepairList', { page: this.page, houseId: this.houseId }).then((response) => {
            if (response.success) {
                if (response.data.list.length > 0) {
                    this.repairList = this.repairList.concat(response.data.list);
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    showMoreClick() {
        this.page++;
        this.getRepairList();
    }
    setHouseId(id) {
        if (this.houseId !== id) {
            this.houseId = id;
            this.tabBarChange(this.selectedLabelDefault);
        }
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            houseSelect
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getRepairType(type) {
                if (type === 'type1') {
                    return '开锁服务';
                }
                else if (type === 'type2') {
                    return '公共报修';
                }
                else if (type === 'type3') {
                    return '个人报修';
                }
                else {
                    return '其他报修';
                }
            }
        }
    })
], Home);
export default Home;
